body {
	background-color: rgba(158, 85, 226, 0.11);
}

.container {
	background-color: rgba(71, 71, 151, 0.164);
margin:auto;
margin-top: 15%;
  width: 25%;
  border: 3px solid green;
  padding: 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.rest { 
	display: flex;
}

.display {
	border: 1px solid black;
}
.numberbuttons {
	margin-top: 20px;
	text-align: center;
	
	height: 100px;
}

.clearAndEquals {
	margin-top: 20px;
	text-align: center;
	
}

.Operations {
	margin-top: 20px;
	text-align: center;
	
}


h1 {
	font-size: large;
}














.numbutton {
	background:linear-gradient(to bottom, #dee6c6 5%, #f0e4ce 100%);
	background-color:#dee6c6;
	border-radius:5px;
	border:1px solid #566963;
	display:inline-block;
	cursor:pointer;
	color:#000000;
	font-family:Arial;
	font-size:10px;
	font-weight:bold;
	padding:7px 11px;
	text-decoration:none;
}
.numbutton:hover {
	background:linear-gradient(to bottom, #f0e4ce 5%, #dee6c6 100%);
	background-color:#f0e4ce;
}
.numbutton:active {
	position:relative;
	top:1px;
}

.operatorbutton {
	background:linear-gradient(to bottom, #ebd4bc 5%, #c79a63 100%);
	background-color:#ebd4bc;
	border-radius:5px;
	border:1px solid #566963;
	display:inline-block;
	cursor:pointer;
	color:#000000;
	font-family:Arial;
	font-size:10px;
	font-weight:bold;
	padding:7px 11px;
	text-decoration:none;
}
.operatorbutton:hover {
	background:linear-gradient(to bottom, #c79a63 5%, #ebd4bc 100%);
	background-color:#c79a63;
}
.operatorbutton:active {
	position:relative;
	top:1px;
}

.clear {
	background:linear-gradient(to bottom, #69bddb 5%, #5cbdcc 100%);
	background-color:#69bddb;
	border-radius:8px;
	display:inline-block;
	cursor:pointer;
	color:#000000;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:5px 20px;
	text-decoration:none;
}
.clear:hover {
	background:linear-gradient(to bottom, #5cbdcc 5%, #69bddb 100%);
	background-color:#5cbdcc;
}
.clear:active {
	position:relative;
	top:1px;
}

.decimal {
	box-shadow:inset 0px 1px 3px 0px #91b8b3;
	background:linear-gradient(to bottom, #768d87 5%, #6c7c7c 100%);
	background-color:#768d87;
	border-radius:5px;
	border:1px solid #566963;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:5px 20px;
	text-decoration:none;
	text-shadow:0px -1px 0px #2b665e;
}
.decimal:hover {
	background:linear-gradient(to bottom, #6c7c7c 5%, #768d87 100%);
	background-color:#6c7c7c;
}
.decimal:active {
	position:relative;
	top:1px;
}